* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  min-height: 100vh;
}

@font-face {
  font-family: "icomoon";
  src: url("../assets/fonts/icomoon/icomoon.eot?jitbdx");
  src: url("../assets/fonts/icomoon/icomoon.eot?jitbdx#iefix")
      format("embedded-opentype"),
    url("../assets/fonts/icomoon/icomoon.ttf?jitbdx") format("truetype"),
    url("../assets/fonts/icomoon/icomoon.woff?jitbdx") format("woff"),
    url("../assets/fonts/icomoon/icomoon.svg?jitbdx#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

